<template>
    <div class="step-title">
      <h4>Нужен ли дисплей водителя?</h4>
    </div>
    <div class="step7">
        <input type="radio" name="chooseTrailer" v-bind:value="1" v-model="selecteDriverMonitor">
        <label>Да</label><br>
    </div>
    <div class="step7">
        <input type="radio" name="chooseTrailer" v-bind:value="0" v-model="selecteDriverMonitor">
        <label>Нет</label><br>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step6')}">Назад</My-Button>
      <My-Button v-bind:disabled="isSelect" @click="nextStep(selecteDriverMonitor)">Вперед</My-Button>
    </div>

</template>
  
<script>
export default {
  name: 'Step-7',
  emits: ['backStep', 'selecteNumberDriverMonitor'],
  components: {
  },
  data() {
    return { 
      numberStep: null,
      selecteDriverMonitor: null,
      isSelect: true
    }
  },
  methods: {
    nextStep(selecteDriverMonitor) {
      this.numberStep = 8
      this.$emit('selecteNumberDriverMonitor', selecteDriverMonitor, this.numberStep,)
    }
  },
  watch: {
    selecteDriverMonitor() {
      if(this.selecteDriverMonitor) this.isSelect = false
    }
  }
}
</script>

