<template>
    <div class="step-title">
      <h4>Меняются ли прицепы (полуприцепы) местами?</h4>
    </div>
    <div class="step6">
        <input type="radio" name="chooseTrailer" v-bind:value="1" v-model="selecteMonitorRS">
        <label>Да</label><br>
    </div>
    <div class="step6">
        <input type="radio" name="chooseTrailer" v-bind:value="0" v-model="selecteMonitorRS">
        <label>Нет</label><br>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step6')}">Назад</My-Button>
      <My-Button v-bind:disabled="isSelect" @click="nextStep(selecteMonitorRS)">Вперед</My-Button>
    </div>

</template>
  
<script>
export default {
  name: 'Step-6',
  emits: ['backStep', 'selecteMonitorRS'],
  components: {
  },
  data() {
    return { 
      numberStep: null,
      selecteMonitorRS: null,
      isSelect: true
    }
  },
  methods: {
    nextStep(selecteMonitorRS) {
      this.numberStep = 7
      this.$emit('selecteMonitorRS', selecteMonitorRS, this.numberStep,)
    }
  },
  watch: {
    selecteMonitorRS() {
      if(this.selecteMonitorRS) this.isSelect = false
    }
  }
}
</script>

