<template>
    <div class="step-title">
      <h4>Количество датчиков на рессорную ось</h4>
    </div>
    <div class="step-grid">
      <div class="step-truck">
        <h5>Автомобиль</h5>
        <div class="step" v-for="number in truckTypeRessor" :key="number">
          <p>{{number}}</p>
          <select class="form-select truckNumberRessorSencor" id="truckType" >
              <option value="1">1</option>
              <option value="2">2</option>
          </select>
        </div>
      </div>
      <div class="step4-trailer">
        <h5>Прицеп</h5>
        <div class="step" v-for="number in trailerTypeRessor" :key="number">
          <p>{{number}}</p>
          <select class="form-select trailerNumberRessorSencor" id="trailerType" >
              <option value="1">1</option>
              <option value="2">2</option>
          </select>
        </div>
      </div>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step5')}">Назад</My-Button>
      <My-Button @click="nextStep">Вперед</My-Button>
    </div>
</template>
  
<script>
export default {
  name: 'Step5-Bl',
  emits: ['backStep', 'numberRessorSensor'], 
  props: {
    truckTypeRessor: {
        type: Number,
        required: true
    },
    trailerTypeRessor: {
        type: Number,
        required: true
    }
},
  components: {
  },
  data() {
    return { 
      numberStep: null,

      numberRessorSensorTruck: 0,
      numberRessorSensorTrailer: 0
    }
  },
  methods: {
    nextStep() {
      let elementsTruckNumberRessorSencor = document.querySelectorAll('.truckNumberRessorSencor');
      for(let i = 0; i < elementsTruckNumberRessorSencor.length; i ++) {
        this.numberRessorSensorTruck = this.numberRessorSensorTruck + (+elementsTruckNumberRessorSencor[i].value)
      }
      let elementsTrailerNumberRessorSencor = document.querySelectorAll('.trailerNumberRessorSencor');
      for(let i = 0; i < elementsTrailerNumberRessorSencor.length; i ++) {
        this.numberRessorSensorTrailer = this.numberRessorSensorTrailer + (+elementsTrailerNumberRessorSencor[i].value)
      }
      this.numberStep = 6
      
      this.$emit('numberRessorSensor',this.numberRessorSensorTruck, 
        this.numberRessorSensorTrailer, this.numberStep )
    },
  },
}
</script>

<style scoped>
.step-grid, .step4-grid-contur{
  margin-bottom: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1rem;
}
.step{
    display: flex;
    align-items: baseline;
}
h5, h6{
  text-align: center;
}
.step >p {
    margin-right: 0.5rem;
}
</style>