<template>
    <div class="step-title">
      <h4>Выберите тип транспортного средства</h4>
    </div>
    <div class="step1" v-for="truck in trucks" :key="truck.id">
        <input type="radio" name="truck" v-bind:value="truck"  v-model="selectedTruck">
        <label>{{truck.truck}}</label><br>
    </div>
    <div class="button-block">
        <My-Button v-bind:disabled="true">Назад</My-Button>
        <My-Button v-bind:disabled="isSelect" @click="nextStep(selectedTruck)">Вперед</My-Button>
    </div>

</template>
  
<script>
export default {
  name: 'Step-1',
  emits: ['selectTruck'],
  props: {
    trucks: {
        type: Array,
        required: true
    },
},
  components: {
  },
  data() {
    return { 
      numberStep: null,
      selectedTruck: null,
      isSelect: true
    }
  },
  methods: {
    nextStep(selectedTruck) {
      if(selectedTruck.trailer == 'trailer') this.numberStep = 2 
      if(selectedTruck.trailer == 'no') this.numberStep = 4
      if(selectedTruck.trailer == 'half-trailer') this.numberStep = 3
      this.$emit('selectTruck', this.numberStep, selectedTruck.axles)
    }
  },
  watch: {
    selectedTruck() {
      if(this.selectedTruck) this.isSelect = false
    }
  }
}
</script>

