<template>
    <div class="step">
        <Step-1 v-if="stepCount == 1"
          :trucks="trucks"
          @selectTruck="selectTruck"
          >
        </Step-1>
        <step-2 v-if="stepCount == 2" 
          :trailers="trailers"
          @backStep="backStep"
          @selectedTrailer="selectedTrailer"
          >
        </step-2>
        <step-3 v-if="stepCount == 3" 
          :halfTrailers="halfTrailers"
          @backStep="backStep"
          @selectedTrailer="selectedTrailer"
          >
        </step-3>
        <step-4 v-if="(stepCount == 4)" 
          :numberTrucsAxles="numberTrucsAxles"
          :numberTrailerAxles="numberTrailerAxles"
          @backStep="backStep"
          @selectedTypeRessorAndContur="selectedTypeRessorAndContur"
          >
        </step-4>
        <step-5 v-if="(stepCount == 5)" 
          :truckTypeRessor="selectTruckTypeRessor"
          :trailerTypeRessor="seleckTrailerTypeRessor"
          @backStep="backStep"
          @numberRessorSensor="numberRessorSensor"
          >
        </step-5>
        <step-6 v-if="(stepCount == 6)" 
          @backStep="backStep"
          @selecteMonitorRS="selecteMonitorRS"
          >
        </step-6>
        <step-7 v-if="(stepCount == 7)" 
          @backStep="backStep"
          @selecteNumberDriverMonitor="selecteNumberDriverMonitor"
          >
        </step-7>
        <step-8 v-if="(stepCount == 8)" 
          @backStep="backStep"
          @selecteTire="selecteTire"
          >
        </step-8>
        <step-9 v-if="(stepCount == 9)" 
          @backStep="backStep"
          @selecteTypeConect="selecteTypeConect"
          >
        </step-9>
        <step-10 v-if="(stepCount == 10)" 
          :numberTrucsAxles="numberTrucsAxles"
          :numberTrailerAxles="numberTrailerAxles"

          :selectTruckTypeRessor="selectTruckTypeRessor"
          :selectTruckTypePnevmo="selectTruckTypePnevmo"
          :seleckTrailerTypeRessor="seleckTrailerTypeRessor"
          :seleckTrailerTypePnevmo="seleckTrailerTypePnevmo"
          :numberConturTruck="numberConturTruck"
          :numberConturTrailer="numberConturTrailer"
          
          :numberRessorSensorTruck="numberRessorSensorTruck"
          :numberRessorSensorTrailer="numberRessorSensorTrailer"
          
          :numberMonitorRS="numberMonitorRS"
          
          :numberDriverMonitor="numberDriverMonitor"
          
          :selecteTireAll="selecteTireAll"
          
          :typeConect="typeConect"
          @backStep="backStep"
          @selecteTypeConect="selecteTypeConect"
          >
        </step-10>
    </div>
</template>
  
<script>
import truck from '@/material/truck';
import trailer from '@/material/trailer';
import halfTrailer from '@/material/halfTrailer';
import Step1 from './Step1.vue';
import Step2 from './Step2.vue';
import Step3 from './Step3.vue';
import Step4 from './Step4.vue';
import Step5 from './Step5.vue';
import Step6 from './Step6.vue';
import Step7 from './Step7.vue';
import Step8 from './Step8.vue';
import Step9 from './Step9.vue';
import Step10 from './Step10.vue';


export default {
  name: 'QuestionsBlock',
  components: {
    Step1,
    Step2,
    Step3,
    Step4,
    Step5,
    Step6,
    Step7,
    Step8,
    Step9,
    Step10
  },
  data() {
    return { 
      stepCount: 1,
      stepArray: [],
      numberTrucsAxles: null,
      numberTrailerAxles: null,

      trucks: truck,
      trailers: trailer,
      halfTrailers: halfTrailer,

      selectTruckTypeRessor: 0,
      selectTruckTypePnevmo: 0,
      seleckTrailerTypeRessor: 0,
      seleckTrailerTypePnevmo: 0,
      numberConturTruck: 0,
      numberConturTrailer: 0,

      numberRessorSensorTruck: 0,
      numberRessorSensorTrailer: 0,

      numberMonitorRS: 0,

      numberDriverMonitor: 0,

      selecteTireAll: null,

      typeConect: 0,
    }
  },

  methods: {
    selectTruck(numberStep, axels) {
      this.stepCount = numberStep
      this.numberTrucsAxles = axels
      this.stepArray.push(numberStep)
    },
    selectedTrailer(axles, numberStep){
      this.numberTrailerAxles = axles
      this.stepCount = numberStep
      this.stepArray.push(numberStep)
    },
    selectedTypeRessorAndContur(selectTruckTypeRessor, selectTruckTypePnevmo, seleckTrailerTypeRessor, seleckTrailerTypePnevmo, numberConturTruck, numberConturTrailer, numberStep){
      this.selectTruckTypeRessor = selectTruckTypeRessor
      this.selectTruckTypePnevmo = selectTruckTypePnevmo
      this.seleckTrailerTypeRessor = seleckTrailerTypeRessor
      this.seleckTrailerTypePnevmo = seleckTrailerTypePnevmo
      this.numberConturTruck = numberConturTruck
      this.numberConturTrailer = numberConturTrailer
      this.stepCount = numberStep
      this.stepArray.push(numberStep)
    },
    numberRessorSensor(numberRessorSensorTruck, numberRessorSensorTrailer, numberStep,){
      this.numberRessorSensorTruck = numberRessorSensorTruck
      this.numberRessorSensorTrailer = numberRessorSensorTrailer

      if(this.numberTrailerAxles) {
        this.stepCount = 6
      } else {
        this.stepCount = 7
      }
      this.stepArray.push(numberStep)
    },
    selecteMonitorRS(selecteMonitorRS, numberStep){
      this.numberMonitorRS = selecteMonitorRS
      this.stepCount = numberStep
      this.stepArray.push(numberStep)
    },
    selecteNumberDriverMonitor(selecteDriverMonitor, numberStep) {
      this.numberDriverMonitor = selecteDriverMonitor
      this.stepCount = numberStep
      this.stepArray.push(numberStep)
    },
    selecteTire(selecteTire, numberStep){
      this.selecteTireAll = selecteTire
      if(this.numberTrailerAxles) {
        this.stepCount = 10
      } else {
        this.stepCount = 9
      }
      this.stepArray.push(numberStep)
    },
    selecteTypeConect(selecteTypeConect, numberStep) {
      this.typeConect = selecteTypeConect
      this.stepCount = numberStep
      this.stepArray.push(numberStep)
    },
    backStep() {
      this.stepArray.pop()
      if(this.stepArray.length == 0) {
        this.stepCount = 1;
      } else {
        this.stepCount = this.stepArray[this.stepArray.length-1]
      }
    }
  }
}

</script>

