<template>
    <div class="step-title">
      <h4>Выберите интерфейс информационной шины</h4>
    </div>
    <div class="step7">
        <input type="radio" name="chooseTrailer" v-bind:value="'RS485 '" v-model="selecteTire">
        <label>RS485 </label><br>
    </div>
    <div class="step7">
        <input type="radio" name="chooseTrailer" v-bind:value="'CAN'" v-model="selecteTire">
        <label>CAN</label><br>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step6')}">Назад</My-Button>
      <My-Button v-bind:disabled="isSelect" @click="nextStep(selecteTire)">Вперед</My-Button>
    </div>

</template>
  
<script>
export default {
  name: 'Step-8',
  emits: ['backStep', 'selecteTire'],
  components: {
  },
  data() {
    return { 
      numberStep: null,
      selecteTire: null,
      isSelect: true
    }
  },
  methods: {
    nextStep(selecteTire) {
      this.numberStep = 9
      this.$emit('selecteTire', selecteTire, this.numberStep,)
    }
  },
  watch: {
    selecteTire() {
      if(this.selecteTire) this.isSelect = false
    }
  }
}
</script>

