<template>
    <div class="step-title">
      <h4>Выбор типа соединения</h4>
    </div>
    <div class="step7">
        <input type="radio" name="chooseTrailer" v-bind:value="'Звезда '" v-model="selecteTypeConect">
        <label>Звезда </label><br>
    </div>
    <div class="step7">
        <input type="radio" name="chooseTrailer" v-bind:value="'T-Cable'" v-model="selecteTypeConect">
        <label>T-Cable</label><br>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step6')}">Назад</My-Button>
      <My-Button v-bind:disabled="isSelect" @click="nextStep(selecteTypeConect)">Вперед</My-Button>
    </div>

</template>
  
<script>
export default {
  name: 'Step-9',
  emits: ['backStep', 'selecteTypeConect'],
  components: {
  },
  data() {
    return { 
      numberStep: null,
      selecteTypeConect: null,
      isSelect: true
    }
  },
  methods: {
    nextStep(selecteTypeConect) {
      this.numberStep = 10
      this.$emit('selecteTypeConect', selecteTypeConect, this.numberStep,)
    }
  },
  watch: {
    selecteTypeConect() {
      console.log(this.selecteTypeConect)
      if(this.selecteTypeConect) this.isSelect = false
    }
  }
}
</script>

