<template>
    <div class="step-title">
      <h4>Выберите тип подвески</h4>
    </div>
    <div class="step4-grid">
      <div class="step4-truck">
        <h5>Автомобиль</h5>
        <div class="step" v-for="number in numberTrucsAxles" :key="number">
          <p>{{number}}</p>
          <select class="form-select truckType" id="number" aria-label="Пример выбора по умолчанию" >
              <option value="ressor">Рессорная подвеска</option>
              <option value="pnevmo">Пневматическая подвеска</option>
          </select>
        </div>
      </div>
      <div class="step4-trailer">
        <h5>Прицеп</h5>
        <div class="step" v-for="number in numberTrailerAxles" :key="number">
          <p>{{number}}</p>
          <select class="form-select trailerType" id="number" aria-label="Пример выбора по умолчанию" >
              <option value="ressor">Рессорная подвеска</option>
              <option value="pnevmo">Пневматическая подвеска</option>
          </select>
        </div>
      </div>
    </div>
    <div class="step4-grid-contur">
      <div class="conturTruck">
        <h6>кол-во контуров</h6>
        <select class="form-select numberConturTruck" id="numberConturTruck" aria-label="Пример выбора по умолчанию">
          <option select value="0">0</option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
        </select>
      </div>
      <div class="conturTrailer">
        <h6>кол-во контуров </h6>
        <select class="form-select numberConturTrailer" id="numberConturTrailer" aria-label="Пример выбора по умолчанию">
          <option value="0">0</option>
          <option value="1">1</option>
          <option value="2">2</option>
          <option value="3">3</option>
          <option value="4">4</option>
          <option value="5">5</option>
        </select>
      </div>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step4')}">Назад</My-Button>
      <My-Button @click="nextStep">Вперед</My-Button>
    </div>
</template>
  
<script>
export default {
  name: 'Step4-Bl',
  emits: ['backStep', 'selectedTypeRessorAndContur'], 
  props: {
    numberTrucsAxles: {
        type: Number,
        required: true
    },
    numberTrailerAxles: {
        type: Number,
        required: true
    }
},
  components: {
  },
  data() {
    return { 
      numberStep: null,

      selectTruckTypeRessor: 0,
      selectTruckTypePnevmo: 0,
      seleckTrailerTypeRessor: 0,
      seleckTrailerTypePnevmo: 0,
      numberConturTruck: 0,
      numberConturTrailer: 0
    }
  },
  methods: {
    nextStep() {
      let elementsTruckType = document.querySelectorAll('.truckType');
      for(let i = 0; i < elementsTruckType.length; i ++) {
        if(elementsTruckType[i].value == 'ressor') this.selectTruckTypeRessor = this.selectTruckTypeRessor+1
        if(elementsTruckType[i].value == 'pnevmo') this.selectTruckTypePnevmo = this.selectTruckTypePnevmo+1
      }
      let elementsTrailerType = document.querySelectorAll('.trailerType');
      for(let i = 0; i < elementsTrailerType.length; i ++) {
        if(elementsTrailerType[i].value == 'ressor') this.seleckTrailerTypeRessor = this.seleckTrailerTypeRessor+1
        if(elementsTrailerType[i].value == 'pnevmo') this.seleckTrailerTypePnevmo = this.seleckTrailerTypePnevmo+1
      }
      let elementsNumberConturTruck = document.getElementById('numberConturTruck');
        this.numberConturTruck = +elementsNumberConturTruck.value
      let elementsNumberConturTrailer = document.getElementById('numberConturTrailer');
        this.numberConturTrailer = +elementsNumberConturTrailer.value
      if(this.selectTruckTypeRessor == 0 || this.selectTruckTypeRessor == 0) {
        this.numberStep = 6
      } else {
        this.numberStep = 5
      }
      
      this.$emit('selectedTypeRessorAndContur',this.selectTruckTypeRessor, 
        this.selectTruckTypePnevmo, this.seleckTrailerTypeRessor, 
        this.seleckTrailerTypePnevmo, this.numberConturTruck, 
        this.numberConturTrailer, this.numberStep, )
    },
  },
}
</script>

<style scoped>
.step4-grid, .step4-grid-contur{
  margin-bottom: 1rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1rem;
}
.step{
    display: flex;
    align-items: baseline;
}
h5, h6{
  text-align: center;
}
.step >p {
    margin-right: 0.5rem;
}
</style>