<template>
    <div class="step-title">
      <h4>Выберите прицеп</h4>
    </div>
    <div class="step2" v-for="trailer in trailers" :key="trailer.id">
        <input type="radio" name="trailer" v-bind:value="trailer"  v-model="selectedTrailer">
        <label>{{trailer.trailer}}</label><br>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step2')}">Назад</My-Button>
      <My-Button v-bind:disabled="isSelect" @click="nextStep(selectedTrailer)">Вперед</My-Button>
    </div>
</template>
  
<script>
export default {
  name: 'Step2-Bl',
  emits: ['backStep', 'selectedTrailer'],
  props: {
    trailers: {
        type: Array,
        required: true
    }
},
  components: {
  },
  data() {
    return { 
      selectedTrailer: null,
      isSelect: true,
      numberStep: null
    }
  },
  methods: {
    nextStep(selectedTrailer) {
      this.numberStep = 4
      this.$emit('selectedTrailer',selectedTrailer.axles, this.numberStep)
    }
  },
  watch: {
    selectedTrailer() {
      if(this.selectedTrailer) this.isSelect = false
    }
  }
}
</script>

