<template>
    <div class="step-title">
      <h4>Выберите полуприцеп</h4>
    </div>
    <div class="step3" v-for="halfTrailer in halfTrailers" :key="halfTrailer.id">
        <input type="radio" name="halfTrailer" v-bind:value="halfTrailer"  v-model="selectedHalfTrailer">
        <label>{{halfTrailer.trailer}}</label><br>
    </div>
    <div class="button-block">
      <My-Button @click="() => {this.$emit('backStep', 'step3')}">Назад</My-Button>
      <My-Button v-bind:disabled="isSelect" @click="nextStep(selectedHalfTrailer)">Вперед</My-Button>
    </div>
</template>
  
<script>
export default {
  name: 'Step3-Bl',
  emits: ['backStep', 'selectedTrailer'],
  props: {
    halfTrailers: {
        type: Array,
        required: true
    }
},
  components: {
  },
  data() {
    return { 
      selectedHalfTrailer: null,
      isSelect: true,
      numberStep: null
    }
  },
  methods: {
    nextStep(selectedHalfTrailer) {
      this.numberStep = 4
      this.$emit('selectedTrailer',selectedHalfTrailer.axles, this.numberStep)
    }
  },
  watch: {
    selectedHalfTrailer() {
      if(this.selectedHalfTrailer) this.isSelect = false
    }
  }
}
</script>

